<template>
  <div class="layout-container">
    <div id="map"></div>
    <task-card class="taskcard" :task="task" type="map" @handleStart="handleStart" @handleCheck="handleCheck"/>
  </div>
</template>
<script>
import TaskCard from "./taskCard"

export default {
  components: {
    TaskCard,
  },
  data() {
    return {
      task: {
        name: '物流单24090801', waypoints: 1, startPosition: '西区1号仓',
        startTime: '2024/09/08 09:00:00', mileage: 25.68, remark: '冰箱4台'
      },
      map: null,

      from: '39.714410,116.507762',
      to: '39.914936,116.478092',
      startPosition: new TMap.LatLng(39.714410, 116.507762),
      endPosition: new TMap.LatLng(39.914936, 116.478092),
      paths: [],

      polylineLayer: null,
      markerLayer: null,
    }
  },
  mounted() {
    this.initMap()
    this.initPath()
  },
  methods: {
    initMap() {
      document.getElementById('map').innerHTML = ''
      this.map = new TMap.Map(document.getElementById('map'), {
        center: this.startPosition,
        zoom: 12,
        showControl: false,
        doubleClickZoom: false,
      })
    },
    initPath() {
      const script = document.createElement('script');
      const callbackName = 'handleDirection' + Date.now();
      window[callbackName] = (data) => {
        const polyline = data.result.routes[0].polyline
        let path = []
        for (let i = 0; i < polyline.length; i++) {
          path[i] = polyline[i]
        }
        let kr = 1000000
        for (let i = 2; i < path.length; i++) {
          path[i] = Math.round(Number(path[i - 2]) * kr + Number(path[i])) / kr;
        }
        this.createPath(path)
      };
      script.src = 'https://apis.map.qq.com/ws/direction/v1/driving/'
        + '?key=' + MAP_KEY
        + '&from=' + this.from
        + '&to=' + this.to
        + '&get_mp=1'
        + '&no_step=1'
        + '&output=jsonp'
        + '&callback=' + callbackName
      document.body.appendChild(script);
    },
    createPath(path) {
      this.paths = []
      let latMin = 90, latMax = 0, lngMin = 180, lngMax = 0
      for (let i = 0; i < path.length; i += 2) {
        let lat = path[i]
        let lng = path[i + 1]
        latMin = lat < latMin ? lat : latMin
        latMax = lat > latMax ? lat : latMax
        lngMin = lng < lngMin ? lng : lngMin
        lngMax = lng > lngMax ? lng : lngMax
        this.paths.push(new TMap.LatLng(lat, lng));
      }
      let latEdge = (latMax - latMin) * 0.1
      let lngEdge = (lngMax - lngMin) * 0.1
      let ne = new TMap.LatLng(latMax + latEdge, lngMax + lngEdge)
      let sw = new TMap.LatLng(latMin - latEdge, lngMin - lngEdge)
      this.map.fitBounds(new TMap.LatLngBounds(sw, ne))

      let map = this.map
      this.polylineLayer = new TMap.MultiPolyline({
        map,
        styles: {
          style_green: new TMap.PolylineStyle({ color: '#13ce66', width: 8, borderWidth: 3, borderColor: '#FFF', showArrow: true }),
          style_gray: new TMap.PolylineStyle({ color: '#ccc', width: 8, borderWidth: 3, borderColor: '#FFF', }),
        },
        geometries: [
          { id: 'path1', styleId: 'style_green', paths: this.paths },
          { id: 'path2', styleId: 'style_green', paths: this.paths }
        ]
      })
      this.markerLayer = new TMap.MultiMarker({
        id: 'car',
        map,
        styles: {
          car1: new TMap.MarkerStyle({ width: 60, height: 90, anchor: { x: 30, y: 45 }, rotation: 90, src: 'https://mapapi.qq.com/web/mapComponents/componentsTest/zyTest/static/model_taxi.png' }),
          start: new TMap.MarkerStyle({ width: 40, height: 60, anchor: { x: 20, y: 60 }, src: 'https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/start.png', }),
          end: new TMap.MarkerStyle({ width: 40, height: 60, anchor: { x: 20, y: 60 }, src: 'https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/end.png', }),
        },
        geometries: [
          { id: 'car', styleId: 'car1', position: this.paths[0] },
          { id: 'start', styleId: 'start', position: this.paths[0] },
          { id: 'end', styleId: 'end', position: this.paths[this.paths.length - 1] }
        ]
      })
    },
    handleStart() {
      this.map.easeTo(
        { center: this.startPosition,zoom: 20, rotation: 90, pitch: 60 },
        { duration: 1000 }
      )
    },
    handleCheck() {
      this.$message({
        message: '距离目的地过远，无法确认送达',
        type: 'warning'
      });
    },
  }
}
</script>
<style scoped>
.layout-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

#map {
  width: 100%;
  height: calc(100% - 430px);
  border-bottom: 1px solid #DCDFE6;
}

.taskcard {
  width: calc(100% - 40px);
  margin: 0;
  border-width: 0;
}
</style>
