  <div class="taskCard">
    <div class="title"><i style="margin-right: 10px" class="el-icon-collection-tag"/>{{ task.name }}</div>
    <div class="content"><i style="margin: 10px" class="el-icon-time"/>出发时间：<span class="contentText">{{ task.startTime }}</span></div>
    <div class="content"><i style="margin: 10px" class="el-icon-location-outline"/>出发地点：<span class="contentText">{{ task.startPosition }}</span></div>
    <div class="content"><i style="margin: 10px" class="el-icon-tickets"/>货品说明：<span class="contentText">{{ task.remark }}</span></div>
    <div class="content"><i style="margin: 10px" class="el-icon-place"/>途经点：<span class="contentText">{{ task.waypoints }}个</span></div>
    <div class="content"><i style="margin: 10px" class="el-icon-odometer"/>总里程：<span class="contentText">{{ task.mileage }}公里</span></div>
    <el-button v-if="this.type === 'task'" type="primary" plain class="button" style="margin-top: -200px" icon="el-icon-video-play" @click="handleStart">开始任务</el-button>
    <el-button v-if="this.type === 'task'" type="info" plain class="button" style="margin-top: -130px" icon="el-icon-alarm-clock">设置提醒</el-button>
    <el-button v-if="this.type === 'task' || this.type === 'history'" type="info" plain class="button" style="margin-top: -60px" icon="el-icon-warning-outline">查看详情</el-button>
