    <el-button v-if="this.type === 'map'" type="warning" plain class="button" style="margin-top: -60px" icon="el-icon-circle-check" @click="handleCheck">确认送达</el-button>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      type: String,
      default: 'task'
